show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\"\n                :page=\"page\"\n                @index=\"ind\"></paging>\n        <!--分页的组件-->\n      </div>\n      <!--<div id=\"pagination\" :class=\"{res:list.length>=9}\">-->\n\n      <!--</div>-->\n    </div>\n    <div class=\"laybox\"\n         style=\"display:none\">\n      <img :src=\"comlist.license\"\n           alt=\"\">\n      <ul>\n        <li>公司名称: {{comlist.company}}</li>\n        <li>负责人: {{comlist.leader}}</li>\n        <li>联系电话: {{comlist.phone}}</li>\n        <li>详细地址: {{comlist.province+comlist.city+comlist.county+comlist.address}}</li>\n        <li>开户行: {{comlist.branch}}</li>\n        <li>提款账号: {{comlist.account}}</li>\n        <li>开户名: {{comlist.bank_name}}</li>\n      </ul>\n    </div>\n    <div class=\"laybox1\"\n         style=\"display:none\">\n      <table class=\"table\">\n        <thead>\n          <tr>\n            <th>公司名称</th>\n            <th>负责人</th>\n            <th>联系电话</th>\n            <th>申请时间</th>\n            <th>开通时间</th>\n            <th>是否新开</th>\n          </tr>\n        </thead>\n        <tbody>\n          <tr v-for=\"item in numlist\">\n            <td>{{item.company}}</td>\n            <td>{{item.leader}}</td>\n            <td>{{item.phone}}</td>\n            <td>{{item.create_time}}</td>\n            <td v-if=\"item.audit_time!=''\">{{item.audit_time|datetime}}</td>\n            <td v-else>--</td>\n            <td>{{item.if_new}}</td>\n          </tr>\n        </tbody>\n      </table>\n    </div>\n    <div class=\"shuju laybox2\"\n         style=\"display:none\">\n      <ul>\n        <li v-if=\"arealist.status==0\">暂未设置供应地区</li>\n        <li v-else\n            v-for=\"item in arealist.data\">\n          {{item.name}}\n          <ul v-if=\"item.son\"\n              class=\"son\">\n            <li v-for=\"sonitem in item.son\">\n              {{sonitem.name}}\n              <ul v-if=\"sonitem.son\"\n                  class=\"grandson\">\n                <li v-for=\"grandson in sonitem.son\">{{grandson.name}}</li>\n              </ul>\n            </li>\n          </ul>\n        </li>\n      </ul>\n    </div>\n    <el-dialog id=\"Dialog\"\n               title=\"罚款详情\"\n               @close=\"close\"\n               :visible.sync=\"dialogVisible\"\n               width=\"50%\"\n               center>\n      <div class=\"dialog_fine\">\n        <table>\n          <thead>\n            <tr>\n              <th>市级配送中心</th>\n              <th>罚款金额</th>\n              <th>物料申请时间</th>\n              <th>货物送达时间 </th>\n              <th>延迟罚款时间</th>\n            </tr>\n          </thead>\n          <tbody>\n            <tr v-for=\"item in Details\">\n              <td>{{item.shop_name}}</td>\n              <td>{{item.fine}}</td>\n              <td>{{item.materiel_time}}</td>\n              <td>{{item.create_time}}</td>\n              <td>{{item.create_time}}</td>\n            </tr>\n          </tbody>\n        </table>\n\n      </div>\n      <span slot=\"footer\"\n            class=\"dialog-footer\">\n\n        <paging :page-count=\"pageCounts\"\n                v-show=\"Details&&Details.length>0\"\n                :page=\"page1\"\n                @index=\"paging\"></paging>\n\n      </span>\n    </el-dialog>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\nimport page from '../common.js';\n\nexport default {\n  data () {\n    return {\n      list: [],\n      dialogVisible: false,\n      token: window.sessionStorage.getItem('bbytoken'),\n      comlist: {},\n      numlist: [],\n      arealist: {},\n      Details: [],\n      //分页数据\n      page: 1, //显示的页码数\n      page1: 1,\n      authList: [],\n      Arr: [1, 2, 3],\n      curId: 0,\n      threeAuthList: [],\n      seCurId: 0,\n      thCurId: 0,\n      pageCount: 1,\n      pageCounts: 1,\n      id: 1,\n    }\n  },\n  destroyed () {\n\n  },\n  methods: {\n    ind (e) { //子组件传过来的事件\n      this.page = e;\n      this.init()\n    },\n    paging (e) { //子组件传过来的事件\n      this.page1 = e;\n\n      this.details(this.id)\n    },\n    init () {\n      this.$axios.post('admin/DelayedFines/index', { token: this.token, page: this.page })\n        .then(res => {\n\n          if (res.data.code == 1) {\n\n            this.list = res.data.data.list;\n            this.pageCount = res.data.data.rows\n          } else {\n            layer.msg(res.data.msg)\n          }\n\n        })\n        .catch(err => console.log(err))\n    },\n    close () {\n      this.pageCounts = 1\n      this.page1 = 1\n      this.Details = []\n    },\n    details (id) {\n\n      this.dialogVisible = true;\n      this.id = id;\n      this.$axios.post(\"admin/DelayedFines/finesDetail\", { token: this.token, page: this.page1, aid: id }).then(res => {\n\n        if (res.data.code == 1) {\n\n          this.Details = res.data.data.list\n          this.pageCounts = res.data.data.rows\n        }\n\n      }).catch(err => console.log(err))\n\n\n    },\n\n    companyf (aid) {\n      this.$axios.post('admin/AgentList/company', { token: this.token, aid: aid })\n        .then(res => {\n\n          this.comlist = res.data.data;\n          if (res.data.data)\n            layer.open({\n              type: 1,\n              area: ['40%', '60%'],\n              title: ['详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n              shadeClose: true,\n              shade: 0.8,\n              closeBtn: 1,  //怎么显示 ×\n              content: $('.laybox')\n            })\n          else layer.msg(res.data.msg)\n        }).catch(err => { })\n    },\n    openshop (aid) {\n      this.$axios.post('admin/AgentList/shopNum', { token: this.token, aid: aid, page: this.index2 })\n        .then(res => {\n\n          this.numlist = res.data.data.list;\n          for (var i = 0; i < res.data.data.rows; i++) {\n            this.pagearr1.push(i + 1);\n          }\n          if (res.data.data.list)\n            layer.open({\n              type: 1,\n              area: ['60%', '50%'],\n              title: ['详情', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n              shadeClose: true,\n              shade: 0.8,\n              closeBtn: 1,  //怎么显示 ×\n              content: $('.laybox1')\n            })\n          else layer.msg(res.data.msg)\n        })\n        .catch(err => console.log(err))\n    },\n\n    regionsf: function (aid) {\n      this.$axios.post('admin/AgentList/regionList', { token: this.token, aid: aid })\n        .then(res => {\n\n          this.arealist = res.data;\n          if (typeof res.data.data == 'object')\n            layer.open({\n              type: 1,\n              area: ['50%', '60%'],\n              title: ['区域', 'height:60px;background-color:#98D1F9;color:white;text-align:center;line-height:60px;font-size:20px;'],\n              shadeClose: true,\n              shade: 0.8,\n              closeBtn: 1,  //怎么显示 ×\n              content: $('.laybox2')\n            })\n          else layer.msg(res.data.data)\n\n        }).catch(err => { })\n    }\n  },\n  created () {\n    this.init()\n\n  },\n  mounted () {\n    var id = this.$route.query.id;\n\n    this.curId = id;\n\n    this.$axios.post('admin/Auth/erAuth', {\n      token: window.sessionStorage.getItem('bbytoken'),\n      id: id\n    })\n      .then(res => {\n\n        if (res.data.code == 1) {\n          var arr = res.data.data;\n\n\n          for (var i = 0; i < arr.length; i++) {\n\n            if (arr[i].name == '管理列表') {\n              this.seCurId = arr[i].id;\n              if (arr[i].son) {\n\n                this.threeAuthList = arr[i].son;\n                for (var j = 0; j < arr[i].son.length; j++) {\n                  if (arr[i].action != arr[i].son[j].action) {\n                    arr[i].action = arr[i].son[0].action;\n                  }\n                  if (arr[i].son[j].name == '市级配送中心列表' && arr[i].name == '管理列表') {\n                    this.thCurId = arr[i].son[j].id;\n                  }\n                }\n              }\n            }\n          }\n          this.authList = arr;\n\n        } else {\n          this.$alert(res.data.msg, '提示', { type: 'error' });\n        }\n      })\n      .catch(err => {\n        alert(err);\n      })\n  },\n  watch: {\n    index () {\n      this.init();\n\n    }\n\n  }\n  //98D1F9\n}\n</script>\n<style >\n#Page {\n  margin-left: 10px;\n}\n/*#pagination{*/\n/*position: absolute;*/\n/*left: ;*/\n/**/\n/*}*/\n.activeClass {\n  color: #04bbfc !important;\n}\n.el-dialog__header {\n  background-color: #98d1f9;\n  padding: 20px 20px 20px;\n}\n.el-dialog__title {\n  color: white;\n}\n.laybox {\n  display: flex;\n}\n\n.laybox img {\n  width: 180px;\n  height: 180px;\n  margin: 115px 7%;\n}\n\n.laybox ul {\n  padding-left: 8%;\n  margin-top: 17px;\n  display: inline-block;\n  border-left: 1px dashed #04bbfc;\n}\n\n.laybox ul li {\n  font-size: 16px;\n  margin-bottom: 40px;\n}\n\n.shuju {\n  margin-top: 16px;\n  font-size: 16px;\n  text-align: center;\n  color: #999;\n  overflow: hidden;\n}\n\n.shuju > ul > li {\n  width: 50%;\n  margin-bottom: 50px;\n}\n\n.shuju .son {\n  margin-left: 20px;\n  display: inline-block;\n  vertical-align: top;\n}\n\n.shuju .son li {\n  display: block;\n}\n\n.shuju .grandson {\n  margin-left: 20px;\n  vertical-align: top;\n  display: inline-block;\n}\n\n.shuju .grandson li {\n  display: block;\n  margin-bottom: 7px;\n}\n.dialog_fine {\n  display: flex;\n}\n.dialog_fine > div {\n  flex: 1;\n  text-align: center;\n}\n.now {\n  background: #3498db !important;\n  color: white !important;\n}\n</style>\n"],"sourceRoot":""}]);

